import { INVALID_MOVE } from 'boardgame.io/core';

function generateBoard() {
  return [
    { name: 'Go', type: 'start' },
    { name: 'Mediterranean Ave', type: 'plot', price: 60 },
    { name: 'Income Tax', type: 'lawsuit', amount: 200 },
    { name: 'Chance', type: 'chance' },
    { name: 'Baltic Ave', type: 'plot', price: 60 },
  ];
}

export const Monopoly = {
  setup: () => ({
    players: [
      { name: "ClosedAI", position: 0, money: 1500, owned_property:[]},
      { name: "Epistem", position: 0, money: 1500, owned_property:[]},
      { name: "Amethyst", position: 0, money: 1500, owned_property:[]},
      { name: "Macrosoft", position: 0, money: 1500, owned_property:[]},
    ],
    dice: null,
    board: generateBoard(),
  }),

  moves: {
    rollDice(G, ctx) {
      const roll = Math.ceil(Math.random() * 6);
      G.dice = roll;
    },

    move(G, ctx) {
      const player = G.players[ctx.currentPlayer];
      player.position = (player.position + G.dice) % G.board.length;
      const square = G.board[player.position];
      
      // simple example: handle what happens when landing on a property
      if (square.type === 'tax') {
        player.money -= square.amount;
      }
      if (square.type === 'property' && !square.owner) {
        // optionally buy it
        if (player.money >= square.price) {
          square.owner = ctx.currentPlayer;
          player.money -= square.price;
        }
      }
    },
  },

  turn: {
    onBegin(G, ctx) {
      G.dice = null; // reset dice roll
    },
    moveLimit: 2, // roll and move
  },

};
